import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudentNotInAcademicWarning = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/AcademicWarning/GetStudentNotInAcademicWarning',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const exportAcademicWarning = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
} = {}) =>
  requestDownload({
    defaultFileName: 'Canh Bao Hoc Tap.xlsx',
    prefix,
    url: '/AcademicWarning/ExportExcelAcademicWarning',
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
    },
  });

export const getStudentInAcademicWarning = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/AcademicWarning/GetStudentInAcademicWarning',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createOrDeleteAcademicWarning = data =>
  request({
    prefix,
    url: '/AcademicWarning/CreateOrDeleteAcademicWarning',
    method: 'POST',
    data,
  });



