import React, { Component } from 'react';
import { Row, Col, Form, Button } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { getSemesters } from 'services/semesterService';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import AcademicWarning from './AcademicWarning';

import './style.scss';

@Form.create({ name: 'formSearchIndividual' })
class FormInfoCommon extends Component {
  state = { paramSearch: {} };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.setState({ paramSearch: { ...values } });
    });
  };

  render() {
    const { getFieldValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
    };

    const { getFieldDecorator } = this.props.form;
    const { paramSearch } = this.state;

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        </Row>
        <Row gutter={24}>
          <Col span={5}>
            <Form.Item label="Năm học:" {...formItemLayout}>
              {getFieldDecorator('yearEduId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn năm học',
                  },
                ],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Học kỳ:" {...formItemLayout}>
              {getFieldDecorator('semesterId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn học kỳ',
                  },
                ],
              })(
                <SelectAjax
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Button type="primary" htmlType="submit" style={{ marginTop: 4 }}>
              Tổng hợp
            </Button>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <AcademicWarning paramSearch={paramSearch} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



