import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row } from 'antd';
import FormInfoCommon from './FormInfoCommon';
import './style.scss';

class WForcedStopSchoolPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Xét cảnh báo và buộc thôi học tín chỉ">
        <Row>
          <FormInfoCommon />
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <WForcedStopSchoolPage />
  </Page>
);



