import React, { Component } from 'react';
import { Button, Row, Col, Table } from 'antd';

class TableAcademicWarning extends Component {
  columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'TBCHK trước',
      dataIndex: 'avgOfPrevSemesterGrade',
      key: 'avgOfPrevSemesterGrade',
    },
    {
      title: 'TBCHK sau',
      dataIndex: 'avgOfAfterSemesterGrade',
      key: 'avgOfAfterSemesterGrade',
    },
    {
      title: 'TBCTL',
      dataIndex: 'semesterLabel',
      key: 'semesterLabel',
    },
    {
      title: 'Năm đào tạo',
      dataIndex: 'yearEduLabel',
      key: 'yearEduLabel',
    },
    {
      title: 'Lý do',
      dataIndex: 'note',
      key: 'note',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => {
        return (
          <Button
            className="btnSmallAddInTable"
            onClick={() => this.props.addStudent(record.studentId)}
          >
            Thêm
          </Button>
        );
      },
    },
  ];

  render() {
    const { studentNotSelectedYet, dataLoading } = this.props;

    return (
      <div>
        <Row>
          <Col span={12}>
            <div>
              Số sinh viên cảnh báo thôi học: <b>{studentNotSelectedYet.length}</b>
            </div>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }} />
        </Row>
        <Table
          columns={this.columns}
          dataSource={studentNotSelectedYet}
          loading={dataLoading}
          rowKey="studentId"
          pagination={false}
        />
      </div>
    );
  }
}

export default TableAcademicWarning;



