import React, { Component } from 'react';
import { Row, Col, Card, Divider, message } from 'antd';
import {
  getStudentInAcademicWarning,
  getStudentNotInAcademicWarning,
  createOrDeleteAcademicWarning,
  exportAcademicWarning,
} from 'services/learningWarningService';
import TableWarningSelected from './TableWarningSelected';
import TableWarningNotSelected from './TableWarningNotSelected';

class AcademicWarning extends Component {
  state = {
    studentNotSelectedYet: [],
    studentSelected: [],
    dataLoading: false,
    paramSearch: [],
    enableExportExcel: false,
  };

  componentWillReceiveProps = nextProps => {
    const { paramSearch } = nextProps;
    if (this.props.paramSearch !== nextProps.paramSearch) {
      this.loadData(paramSearch);
    }
  };

  loadData = async paramSearch => {
    this.setState({ dataLoading: true });
    const res1 = await getStudentInAcademicWarning(paramSearch);
    const res2 = await getStudentNotInAcademicWarning(paramSearch);
    this.setState({
      paramSearch,
      enableExportExcel: res1.data.items.length > 0,
      studentSelected: res1.data.items,
      studentNotSelectedYet: res2.data.items,
      dataLoading: false,
    });
  };

  addStudent = studentId => {
    const { studentSelected, studentNotSelectedYet } = this.state;
    // remove item in student not selected yet
    const index = studentNotSelectedYet.findIndex(student => student.studentId === studentId);
    const student = studentNotSelectedYet[index];
    studentNotSelectedYet.splice(index, 1);
    studentSelected.push(student);
    this.setState({ studentNotSelectedYet, studentSelected });
  };

  removeStudent = studentId => {
    const { studentSelected, studentNotSelectedYet } = this.state;
    // remove item in student not selected yet
    const index = studentSelected.findIndex(student => student.studentId === studentId);
    const student = studentSelected[index];
    studentSelected.splice(index, 1);
    studentNotSelectedYet.push(student);
    this.setState({ studentSelected, studentNotSelectedYet });
  };

  exportAcademicWarning = () => {
    const { paramSearch } = this.state;
    exportAcademicWarning(paramSearch);
  };

  saveListStudentSelected = async () => {
    const { paramSearch } = this.state;
    const dataToSave = this.prepareDataForSave();
    await createOrDeleteAcademicWarning(dataToSave);
    this.loadData(paramSearch);
    message.success('Lưu thành công');
  };

  prepareDataForSave = () => {
    const { studentSelected, paramSearch } = this.state;
    return studentSelected.map(student => {
      const { studentId, note, avgGrade } = student;
      const { semesterId, yearEduId } = paramSearch;
      const obj = {
        studentId,
        semesterId,
        yearEduId,
        note,
        avgGrade,
      };
      return obj;
    });
  };

  render() {
    const { studentNotSelectedYet, studentSelected, dataLoading, enableExportExcel } = this.state;
    console.log('enableExportExcel', enableExportExcel);
    return (
      <Card>
        <Divider>Danh sách sinh viên chưa chọn</Divider>
        <Row>
          <Col span={24}>
            <TableWarningNotSelected
              studentNotSelectedYet={studentNotSelectedYet}
              dataLoading={dataLoading}
              addStudent={this.addStudent}
            />
          </Col>
        </Row>
        <Divider>Danh sách sinh viên đã chọn</Divider>
        <Row className="rowMargin">
          <Col span={24}>
            <TableWarningSelected
              enableExportExcel={enableExportExcel}
              exportAcademicWarning={this.exportAcademicWarning}
              saveListStudentSelected={this.saveListStudentSelected}
              studentSelected={studentSelected}
              dataLoading={dataLoading}
              removeStudent={this.removeStudent}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default AcademicWarning;



