import React, { Component } from 'react';
import { BtnSave, BtnExport } from 'components/common/button';
import { Button, Row, Col, Table } from 'antd';

class TableAcademicWarning extends Component {
  columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'TBCHK trước',
      dataIndex: 'avgOfPrevSemesterGrade',
      key: 'avgOfPrevSemesterGrade',
    },
    {
      title: 'TBCHK sau',
      dataIndex: 'avgOfAfterSemesterGrade',
      key: 'avgOfAfterSemesterGrade',
    },
    {
      title: 'TBCTL',
      dataIndex: 'semesterLabel',
      key: 'semesterLabel',
    },
    {
      title: 'Năm đào tạo',
      dataIndex: 'yearEduLabel',
      key: 'yearEduLabel',
    },
    {
      title: 'Lý do',
      dataIndex: 'note',
      key: 'note',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Button
          className="btnSmallDeleteInTable"
          onClick={() => this.props.removeStudent(record.studentId)}
        >
          Xóa
        </Button>
      ),
    },
  ];

  render() {
    const {
      studentSelected,
      saveListStudentSelected,
      dataLoading,
      exportAcademicWarning,
      enableExportExcel,
    } = this.props;

    return (
      <div>
        <Row>
          <Col span={12}>
            <div>
              Tổng số sinh viên: <b>{studentSelected.length}</b>
            </div>
          </Col>
          <Col style={{ textAlign: 'right' }}>
            <BtnSave title="Lưu cảnh báo học tập" onClick={() => saveListStudentSelected()} />
            <BtnExport
              title="Xuất cảnh báo học tập"
              onClick={() => exportAcademicWarning()}
              disabled={!enableExportExcel}
            />
          </Col>
        </Row>
        <Table
          columns={this.columns}
          dataSource={studentSelected}
          loading={dataLoading}
          rowKey="studentId"
          pagination={false}
        />
      </div>
    );
  }
}

export default TableAcademicWarning;



